import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			tipShow: false,
			user:[],
			isSwitch: false
		};
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	mounted() {
		this.user = this.$store.getters.getUserInfo
		// console.log(this.user)
	},
	methods: {
		navTo(url) {
			uni.navigateTo({
				url
			})
		},
		logout() {
			const that = this
			uni.showModal({
				title: '询问',
				content: '您确定要退出吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: (e) => {
					if (e.confirm) {
						that.$store.commit('logout')
						that.$api.logout()
						setTimeout(() => {
							uni.navigateBack();
						}, 200)
					}
				}
			})
		},
		SwitchA(e) {
			this.isSwitch = true
			const flag = e.detail.value?0:1
			this.updateUser(flag)
			if(flag==1) {
				uni.showModal({
					title: '关闭提示',
					content: '关闭后将无法接收到竞赛推送、组队提醒等功能',
					showCancel: false,
					confirmText: '知道了',
					success: (e) => {
						if (e.confirm) {
						}
					}
				})
			} else if(flag==0) {
				uni.showModal({
					title: '提示',
					content: '接收推送请关注‘i瓜大’公众号',
					showCancel: false,
					confirmText: '知道了',
					success: (e) => {
						if (e.confirm) {
						}
					}
				})
			}
		},
		showTip() {
			this.tipShow = true
		},
		async updateUser(flag) {
			const res = await this.$api.request({
				url: '/user/update/' + this.user.id,
				method: "PUT",
				contentType: "application/json",
				data: {
					showMe: flag
				}
			})
			console.log(res)
			this.user.showMe = flag
			this.$store.commit("updateUserInfo", this.user)
			console.log(this.isSwitch)
			if(this.isSwitch && res.status==200) {
				this.isSwitch = false
				return
			}
			this.$api.msg(res.msg)
			
		},
		clear(){
			this.$store.commit('logout')
			this.$api.logout()
			this.$api.msg("清除缓存成功~")
		},
	}
}
